* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
    box-sizing: border-box;
}


html{
    scroll-behavior: smooth;
}


body{

    font-family: Calibri, Helvetica, sans-serif;
    color: white;  
}





#header{
    z-index: 10;
    position: fixed;
    width: 100%;
    overflow: hidden;
    background-color: black;
}

.title{
    font-size: 5vh;
    font-weight: 700;
    margin-left: 1vh;
    text-align: center; 
    color:white; 
}

.titleSubpage{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 6vh;
    font-weight: 700;
    font-size:6vh;
    font-size: min(6vh, 6vw);
    animation: wobble 6s linear infinite;
}

@keyframes wobble{
    0%{
        transform: translateY(0px) rotate(0deg);
    }
    25%{
        transform: translateY(0.5px) rotate(1deg);
    }
    50%{
        transform: translateY(1px) rotate(0deg);
    }
    75%{
        transform: translateY(0.5px) rotate(-1deg);
    }
    100%{
        transform: translateY(0px) rotate(0deg);
    }
}



.navlistItems {
    font-weight: 600;
    font-size: 3vh;
    margin: 1vh 1vw;
    list-style : none;
    color: white;
}
.navlistItems:hover {
    text-decoration: underline;
    cursor: pointer;
}


.navlist{
    float: right;
    display: flex;
}



#projectsPage{
    padding-top: 6vh;
    overflow: hidden;
    background-image: url('/static/images/Projects.jpg');
    background-size: cover;
}

#allSliders{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}


.sliderGridChild{
    display: grid;
    grid-template: 80px 5fr / 100%;
    margin-top: 5vw;
    overflow: hidden;
    max-width: 90vw;
    margin-bottom: 2vh;
}


.sliderContent{
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    justify-content: center;
    

}


.sliderText{
    display: flex;
    max-width: 40%;
    min-width: 500px;
    text-align: center;
    justify-content: center;
    justify-items: center;
    font-size: 20px;
    align-items: center;
    letter-spacing: 0.1vw;
    line-height: 2;
    

}
.sliderWindow{
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden; 
    margin-right: 5%;
    max-height: 60vh;
    max-width:40vw;
    min-width: 500px;
}

.sliderImg{
    z-index: 1;
    overflow: hidden;
    width:100%;
    height: 100%;
    border: white solid 2px;
}

.sliderLeftBtn, .sliderRightBtn{
    display: flex;
    position: absolute;
    height: 2em;
    width: 2em;
    font-weight: 1000;
    font-size: 3vh;
    align-content: center;
    justify-content: center;
    background-color: rgb(0,0,0,0.5);
}

.sliderLeftBtn{
    z-index: 2;
    border-left: white solid 2px;
}

.sliderRightBtn{
    z-index: 3;
    align-self: flex-end;
    border-right: white solid 1.5px;
}

.sliderLeftBtn:hover, .sliderRightBtn:hover{
    cursor: pointer;
    color: aquamarine;
}




.sliderTitle{
    grid-area: 1 / 1/ span 1/-1;
    justify-self: center;
}

.titles{
   display: flex;
   justify-items: center;
   justify-content: center;
   align-content: center; 
}


.sliderTitle{
    color: white;
    font-weight: 500;
    justify-content: center;
    display: flex;
    align-self: center;
    
}
a{
    text-decoration: none;
}


 .sliderTitle{
    font-size:4vh;
    font-size: min(30px, 4vw);
    
}

.slidersubtitle{
    font-size: 17px;
    font-weight: 700;
    margin-top:10px;
    color: white;
    justify-content: center;
    display: flex;
    margin-bottom: 2vh;
}
.slidersubtitle:hover{
    text-decoration: underline;
}

/*Scrollbar*/
::-webkit-scrollbar{
    width: 20px;
}

::-webkit-scrollbar-track{
    box-shadow: inset 0 0 5px black; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb{
    background: darkred;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover{
    background: red;
    border-radius: 10px;
}




